<template>
    <v-container >
        <h2>Contact form</h2>

        <v-expansion-panels dark>
            <v-expansion-panel hover focusable inset >
                <v-expansion-panel-header>
                    <p>
                        If you wish to contact me and I've bailed out of any and all social media, or you have, or we just don't have an overlap
                        in such more “conventional” ways of communication, you may use the form below. Do include a contact form of your own if I
                        am to get back to you!
                    </p>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <p>
                        This form may not work at times due to stringent anti-spam measures; if it's disabled or errors out, refresh the page later.
                        And when it does, I may not immediately receive your message; it'll be in a database somewhere.
                    </p>
                    <p>
                        You may find me on (in decreasing order of preference):
                    </p>
                    <ul>
                        <li>Discord</li>
                        <li>generic email</li>
                        <li>LinkedIn</li>
                        <li>WhatsApp</li>
                        <li>(literally anything else)</li>
                        <li>Facebook</li>
                    </ul>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <p></p>

        <span v-if="formAvailable">
            <v-text-field dark filled outlined v-model="title" label="Title" :disabled="!formElementsEnabled" />

            <v-textarea dark filled outlined v-model="content" label="Message" :disabled="!formElementsEnabled" />

            <v-btn :disabled="!formElementsEnabled" :loading="restCallsGoing > 0"
                   width="inherit" elevation="7" x-large dark
                   @click="submitMessage()">
                Send
            </v-btn>
        </span>

        <p v-else>
            The contact form is not available from here. Go to the
            <a href="https://teonistor.herokuapp.com/#/contact"> full version of the website </a>
            which has a backend to receive it.
        </p>

        <Notifications :alerts="alerts" :floating="true" />
    </v-container>
</template>

<script>
  import axios from 'axios';
  import Notifications from '../components/Notifications.vue';

  export default {
    components: {
      Notifications
    },

    props: ['formAvailable'],

    data: () => ({
      alerts: [],

      restCallsGoing: 0,
      formEnabled: false,

      title: '',
      content: ''

    }),

    computed: {
      formElementsEnabled () {
        return this.formEnabled && !this.restCallsGoing;
      }
    },

    methods: {
      submitMessage () {
        this.axiosHelper('submitting form', () => axios.post(this.$apiUrl + '/contact', {
          title: this.title,
          content: this.content,
          receivedFrom: location.host
        }), ignore => this.alerts.push({type: 'info', text: 'Got it!'}))
          .then(() => this.checkEnabled());
      },

      checkEnabled() {
        this.axiosHelper('checking whether the form is enabled', () => axios.get(this.$apiUrl + '/contact/enabled'),
          data => this.formEnabled = data);
      },

      // TODO Integrate into a component... see in Chess repo and elsewhere
      axiosHelper(flowDescription, promiseProducer, dataConsumer) {
        // dataConsumer is optional... because I said so
        if (!dataConsumer)
          return this.axiosHelper(flowDescription, promiseProducer, () => {});

        this.restCallsGoing++;
        return promiseProducer()
          .then(response => dataConsumer(response.data))
          .catch(error => {
            let errorLog = 'Error ' + flowDescription;
            console.log(errorLog, error);
            let errorCode = error.response && error.response.status && ' (' + error.response.status + ')' || '';
            let errorDetail = error.response && error.response.data || error.toString();
            this.alerts.push({type: 'warning', text: errorLog + errorCode + ': ' + errorDetail});
          })
          .then(() => this.restCallsGoing--);
      }
    },

    mounted () {
      this.checkEnabled();
    }
  }
</script>

